import React, { useState } from 'react'
import RenderNotes from './RenderNotes'
import AddUpdateNotes from './AddUpdateNotes'

const Notes = () => {
    const [isShowHideAddNotes,setIsShowHideAddNotes]=useState(false)
    const [isShowAddUpdateNotes,setIsShowAddUpdateNotes]=useState(false)
    const toggleAddNotes =()=>{
        setIsShowAddUpdateNotes(!isShowAddUpdateNotes)
    }
    
  return (
    <div className="cardBox myRequest mb-3">
    <div className="cardHead border-bottom pb-3">
      <h3>
        <img
          src="images/other-attendance.svg"
          alt=""
          width={40}
          className="me-2 fw-bold"
        />{" "}
        My Performance notes
      </h3>
    </div>
    <div className="d-flex justify-content-between align-items-center border-bottom py-3 mb-3">
      <div className="d-flex align-items-center dateFilterSelect gap-2">
        <div className="searchB">
          <input type="text" className="form-control" placeholder="Search" />
        </div>
        <div className="addNotes">
          <button
            type="button"
            className={`addBtn ${isShowHideAddNotes ?"open":''}`}
            data-bs-toggle="tooltip"
            data-bs-title="Add Note"
            onClick={()=>setIsShowHideAddNotes(!isShowHideAddNotes)}
          >
            <i className="bi bi-plus" />
          </button>
          {true &&<ul id="addNoteList" className={`${isShowHideAddNotes ?"show":''}`}>
            <li>
              <button
                type="button"
                style={{ backgroundColor: "#D1ADFC" }}
                onClick={()=>toggleAddNotes()}
              />
            </li>
            <li>
              <button
                type="button"
                style={{ backgroundColor: "#A8FF98" }}
                onClick={()=>toggleAddNotes()}
              />
            </li>
            <li>
              <button
                type="button"
                style={{ backgroundColor: "#B3D7F4" }}
                onClick={()=>toggleAddNotes()}
              />
            </li>
            <li>
              <button type="button" style={{ backgroundColor: "#FFDAA5" }} />
            </li>
          </ul>}
        </div>
      </div>
      <div className="d-flex dateFilterSelect gap-2">
        <select className="form-control quarterSelect form-select">
          <option>Quarter 1 (Apr - Jun)</option>
          <option>Quarter 1 (Apr - Jun)</option>
          <option>Quarter 1 (Apr - Jun)</option>
        </select>
        <select className="form-control form-select">
          <option>2023</option>
          <option>2022</option>
          <option>2021</option>
        </select>
      </div>
    </div>
        <RenderNotes/>
        <AddUpdateNotes
        openModel={isShowAddUpdateNotes}
        handleOpenClose={toggleAddNotes}
        />
  </div>
  
  )
}

export default Notes
