import React from 'react'
import { useCustomTranslation } from '../../../../translation'
import { CustomButton } from '../../../globalComponents'

const AddUpdateNotes = (props) => {
    const { t } = useCustomTranslation()
    const { openModel, handleOpenClose } = props

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    // width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {t("headings.addNewKRA")}
                    </h5>
                    <button type="button"
                        className="btn-close"
                        data-bs-dismiss="offcanvas"
                        aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="formPanel">
                            <>
                                <div className="form-group mb-3">
                                    <label htmlFor="" className="text-primary fw-medium">
                                        Enter Heading
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        name=""
                                        id=""
                                        placeholder="Enter here"
                                    />
                                </div>
                                <div className="form-group mb-3">
                                    <label htmlFor="" className="text-primary fw-medium">
                                        Note Body
                                    </label>
                                    <textarea
                                        className="form-control"
                                        placeholder="Enter Reason"
                                        rows={10}
                                        defaultValue={""}
                                    />
                                </div>
                            </>

                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <div className="d-flex align-items-center justify-content-end gap-3">
                            <CustomButton
                                text={"back"}
                                classNames="border-0 p-0 bg-transparent cancelBtn"
                            />
                            <CustomButton
                                text={"Save Note"}
                                classNames="btn btn-primary gradientBg"
                            />


                        </div>

                       
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateNotes
